<template>
  <div class="logo-grid__header">
    <div class="h3 logo-grid__title">
      {{ section.settings.heading_text }}
    </div>

    <div class="h6 logo-grid__subtext">
      {{ section.settings.subtext }}
    </div>
  </div>

  <swiper
    :auto-height="swiperSettings.autoHeight"
    :center-insufficient-slides="swiperSettings.centerInsufficientSlides"
    :prevent-interaction-on-transition="swiperSettings.preventInteractionOnTransition"
    :keyboard="swiperSettings.keyboard"
    :modules="modules"
    :autoplay="{enabled: !isMobile, delay: swiperSettings.delay}"
    :loop="swiperSettings.loop"
    :speed="swiperSettings.speed"
    :breakpoints="swiperSettings.breakpoints"
    :slides-per-view="swiperSettings.slidesPerView"
    :space-between="swiperSettings.spaceBetween"
    :threshold="swiperSettings.threshold"
    :class="['logo-grid__logos--smooth']"
  >
    <template
      v-for="(logo, index) in logos"
      :key="`logo-slide-${index}`"
    >
      <swiper-slide
        :id="`logo-slide-${index}`"
        tag="a"
        target="_blank"
        :class="[
          'logo-grid__item',
          'responsive-image__wrapper',
          'lazyloaded',
          'swiper-slide'
        ]"
        :href="logo.link"
        :aria-label="logo.title"
      >
        <img
          class="logo-grid__logo lazyload blur-up"
          :src="logo.fileUrl"
          :alt="logo.title"
          data-sizes="auto"
          :data-src="logo.fileUrl"
          data-widths="[180, 360, 540, 720, 900, 1080, 1296, 1512, 1728, 1944, 2160, 2376, 2592, 2808, 3024]"
        >
      </swiper-slide>
    </template>
  </swiper>
</template>

<script>

/**
 * Vue: Logo grid (logo-grid)
 * -----------------------------------------------------------------------------
 * Slider carousel for logos.
 *
 * @param {String} liquid.sectionId - Section's Liquid ID.
 *
 */
import { Swiper, SwiperSlide } from 'swiper/vue'
import { Autoplay } from 'swiper'
import { mapState } from 'vuex'

export default {
  name: 'LogoGrid',

  components: {
    Swiper,
    SwiperSlide,
  },

  props: {
    section: {
      type: Object,
      default: () => {},
    },
    logos: {
      type: Object,
      default: () => ({
        sectionId: '',
      }),
    },
  },

  setup() {
    return {
      modules: [Autoplay],
    }
  },

  data() {
    return {
      swiperSettings: {
        autoHeight: false,
        speed: this.section.settings.autoplaySpeed * 1000,
        breakpoints: {
          '768': {
            spaceBetween: 24,
          },
        },
        centerInsufficientSlides: true,
        keyboard: true,
        loop: true,
        preventInteractionOnTransition: true,
        slidesPerView: 'auto',
        spaceBetween: 16,
        threshold: 5,
      },
    }
  },

  computed: {

    /**
     * Map Vuex state.
     */
    ...mapState({
      isMobile: (state) => state.index.screen.mobile,
    }),
  },

}
</script>

<style lang="scss">
  @import './logo-grid';
</style>
